import Vue from 'vue';
import { sleep } from '../commons';

/**
 * Metodos utilitários globais.
 */
Vue.mixin({
    methods: {
        $alert(msg) {
            this.$store.commit('ui/ALERT', msg);
        },

        $notify(msg) {
            this.$store.commit('ui/NOTIFY', msg);
        },

        $sidebarToggle(open) {
            this.$store.commit('ui/SIDEBAR_TOGGLE');
        },

        $quickbarToggle(open) {
            this.$store.commit('ui/QUICKBAR_TOGGLE', open);
        },

        $quickbarOpen(view, options) {
            this.$store.commit('ui/QUICKBAR_OPEN', { view, options });
        },

        async $sleep(ms) {
            return sleep(ms);
        },

        $moment(args) {
            return window.moment(args);
        },
    },
});
