import themes from './libs/vuetify/themes';
import components from './views/data/components';
import { folders } from './views/data/structures';
import { lua } from './libs/vuetify/themes';
import { sleep } from './commons';

const defaultValues = {
    quickbar: {
        open: false,
        view: null,
        options: {
            title: null,
            icon: null,
            width: 320,
            overlay: true,
            autoclose: true
        }
    },
};

export default {
    namespaced: true,

    state: {
        theme: lua,

        themes,

        message: {
            alert: {
                title: 'message.alert.title',
                text: null,
                visible: false,
            },
            notify: {
                text: null,
                visible: false,
                color: 'warning',
                timeout: 5000
            },
        },

        sidebar: {
            open: false,
        },

        quickbar: {
            ...defaultValues.quickbar
        },

        messenger: {
            window: 0,
        },

        docs: {
            components,
            folders,
        },
    },

    mutations: {
        SET_THEME(state, theme) {
            state.theme = theme;
        },
        ALERT(state, msg) {
            if (typeof msg === 'object') {
                return state.message.alert = {
                    ...state.message.alert,
                    ...msg
                };
            }
            state.message.alert.text = msg;
            state.message.alert.visible = true;
        },
        NOTIFY(state, msg) {
            if (typeof msg === 'object') {
                return state.message.notify = {
                    ...state.message.notify,
                    ...msg
                };
            }
            state.message.notify.text = msg;
            state.message.notify.visible = true;
        },
        SIDEBAR_TOGGLE(state) {
            state.sidebar.open = !state.sidebar.open;
        },
        QUICKBAR_TOGGLE(state, open) {
            state.quickbar.open = open !== undefined ? open : !state.quickbar.open;
        },
        QUICKBAR_OPEN(state, payload) {
            if (!payload || !payload.view) {
                return console.log('[X] Informe o nome do componente a ser renderizado na quickbar.');
            }
            if (payload.options) {
                state.quickbar.options = {...state.quickbar.options, ...payload.options };
            }
            state.quickbar.view = payload.view;
            state.quickbar.open = true;
        },
        QUICKBAR_RESET(state) {
            sleep(300).then(() => {
                state.quickbar = { ...defaultValues.quickbar };
            });
        },
        SET_MESSENGER_WINDOW(state, window) {
            state.messenger.window = window;
        },
    },

    actions: {

    },
};
