        SEND_MESSAGE(state, msg) {
            state.messenger.sequence = state.messenger.sequence + 1;
            msg.id = state.messenger.sequence;
            msg.sendDate = window.moment();
            msg.right = true;
            msg.from = {
                id: state.user.id,
                name: state.user.name,
                alias: state.user.name.split(' ')[0],
                profile: state.user.profile
            };
            msg.to = {
                id: state.messenger.user.id,
                name: state.messenger.user.name,
                alias: state.messenger.user.alias,
                profile: state.messenger.user.profile
            };
            state.messenger.user.messages.push(msg);

            setTimeout(() => {
                state.messenger.sequence = state.messenger.sequence + 1;
                const _msg = JSON.parse(JSON.stringify(msg));

                state.messenger.user.messages.push({
                    id: state.messenger.sequence,
                    sendDate: window.moment(),
                    text: `Olá ${_msg.from.alias}, recebi sua mensagem e logo estarei retomando esse assunto.`,
                    right: false,
                    from: _msg.to,
                    to: _msg.from
                });
            }, 1300);
        },
